import { Component, OnInit} from '@angular/core';
import { Item} from '../Items';
import { Subscription } from 'rxjs';
import {MessageService} from '../common.service'
import {CartlistComponent} from '../cartlist/cartlist.component'
@Component({
  selector: 'app-items',
  templateUrl: './items.component.html',
  styleUrls: ['./items.component.css'],
  providers:[CartlistComponent]
})
export class ItemsComponent implements OnInit {
  items: Item[];
  check= true;
  subscription: Subscription;
  constructor(private messageService: MessageService,private cartlist :CartlistComponent) {
    this.subscription = this.messageService.getMessage().subscribe(message => {
        this.check = message.text
        console.log(this.check)
      });
  }
  ngOnInit() {
    this.getDataes();   
    
  }

  getDataes(): void {
    this.messageService.getDatas()
      .subscribe((data: Item[]) => {
        this.items = data;
      });
  }

  addCart(id:string,qty:string) : void {
    this.messageService.addToCart(id,qty)
      .subscribe(data => {
        if(data['auth']=="fail"){
          window.open("/login/", '_self');
        }else{
          alert("Added to cart")
        }
        this.cartlist.getCartList()
      });    
  
 }


}
